      </h2>
      <p class="mt-2 text-center text-sm text-gray-600">
        –£–∂–µ –µ—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç?
        <router-link 
          to="/login" 
          class="font-medium text-blue-600 hover:text-blue-500"
        >
          –í–æ–π—Ç–∏
        </router-link>
      </p>
    </div>

    <div class="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
      <div class="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
        <!-- Social Register Buttons -->
        <div class="space-y-3 mb-6">
          <button
            type="button"
            class="w-full flex justify-center items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <img src="https://img.icons8.com/color/20/google-logo.png" class="mr-2" />
            –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —á–µ—Ä–µ–∑ Google
          </button>
          
          <button
            type="button"
            class="w-full flex justify-center items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <img src="https://img.icons8.com/ios-glyphs/20/github.png" class="mr-2" />
            –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —á–µ—Ä–µ–∑ GitHub
          </button>
        </div>

        <div class="relative mb-6">
          <div class="absolute inset-0 flex items-center">
            <div class="w-full border-t border-gray-300" />
          </div>
          <div class="relative flex justify-center text-sm">
            <span class="px-2 bg-white text-gray-500">–ò–ª–∏ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è —Å email</span>
          </div>
        </div>

        <!-- Register Form -->
        <form class="space-y-6" @submit.prevent="handleRegister">
          <!-- Error message -->
          <div v-if="authStore.error" class="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded">
            {{ authStore.error }}
          </div>

          <div>
            <label for="fullName" class="block text-sm font-medium text-gray-700">
              –ü–æ–ª–Ω–æ–µ –∏–º—è
            </label>
            <div class="mt-1">
              <input
                id="fullName"
                name="fullName"
                type="text"
                autocomplete="name"
                required
                v-model="form.fullName"
                class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="–ò–≤–∞–Ω –ò–≤–∞–Ω–æ–≤"
                :disabled="authStore.isLoading"
              />
            </div>
          </div>

          <div>
            <label for="email" class="block text-sm font-medium text-gray-700">
              Email –∞–¥—Ä–µ—Å
            </label>
            <div class="mt-1">
              <input
                id="email"
                name="email"
                type="email"
                autocomplete="email"
                required
                v-model="form.email"
                class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="your.email@example.com"
                :disabled="authStore.isLoading"
              />
            </div>
          </div>

          <div>
            <label for="password" class="block text-sm font-medium text-gray-700">
              –ü–∞—Ä–æ–ª—å
            </label>
            <div class="mt-1 relative">
              <input
                id="password"
                name="password"
                :type="showPassword ? 'text' : 'password'"
                autocomplete="new-password"
                required
                v-model="form.password"
                class="appearance-none block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="–ú–∏–Ω–∏–º—É–º 8 —Å–∏–º–≤–æ–ª–æ–≤"
                :disabled="authStore.isLoading"
              />
              <button
                type="button"
                class="absolute inset-y-0 right-0 pr-3 flex items-center"
                @click="showPassword = !showPassword"
              >
                <span class="text-gray-400 hover:text-gray-600">
                  {{ showPassword ? 'üôà' : 'üëÅÔ∏è' }}
                </span>
              </button>
            </div>
            <!-- Password strength indicator -->
            <div class="mt-1">
              <div class="flex space-x-1">
                <div 
                  v-for="i in 4" 
                  :key="i"
                  class="h-1 w-1/4 rounded"
                  :class="getPasswordStrengthClass(i)"
                ></div>
              </div>
              <p class="text-xs text-gray-500 mt-1">
                {{ passwordStrengthText }}
              </p>
            </div>
          </div>

          <div>
            <label for="confirmPassword" class="block text-sm font-medium text-gray-700">
              –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å
            </label>
            <div class="mt-1">
              <input
                id="confirmPassword"
                name="confirmPassword"
                type="password"
                autocomplete="new-password"
                required
                v-model="form.confirmPassword"
                class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
                :disabled="authStore.isLoading"
              />
            </div>
            <p v-if="form.confirmPassword && !passwordsMatch" class="text-xs text-red-500 mt-1">
              –ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç
            </p>
          </div>

          <div class="flex items-center">
            <input
              id="agree-terms"
              name="agree-terms"
              type="checkbox"
              required
              v-model="form.agreeTerms"
              class="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label for="agree-terms" class="ml-2 block text-sm text-gray-900">
              –Ø —Å–æ–≥–ª–∞—Å–µ–Ω —Å 
              <a href="#" class="text-blue-600 hover:text-blue-500">—É—Å–ª–æ–≤–∏—è–º–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è</a>
              –∏ 
              <a href="#" class="text-blue-600 hover:text-blue-500">–ø–æ–ª–∏—Ç–∏–∫–æ–π –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç–∏</a>
            </label>
          </div>

          <div>
            <button
              type="submit"
              :disabled="authStore.isLoading || !isFormValid"
              class="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <span v-if="authStore.isLoading" class="mr-2">‚è≥</span>
              {{ authStore.isLoading ? '–°–æ–∑–¥–∞–Ω–∏–µ –∞–∫–∫–∞—É–Ω—Ç–∞...' : '–°–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç' }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { useAuthStore } from '@/stores/auth';

const router = useRouter();
const authStore = useAuthStore();

const form = ref({
  fullName: '',
  email: '',
  password: '',
  confirmPassword: '',
  agreeTerms: false,
});

const showPassword = ref(false);

const passwordsMatch = computed(() => {
  return form.value.password === form.value.confirmPassword;
});

const passwordStrength = computed(() => {
  const password = form.value.password;
  let strength = 0;
  
  if (password.length >= 8) strength++;
  if (/[a-z]/.test(password)) strength++;
  if (/[A-Z]/.test(password)) strength++;
  if (/[0-9]/.test(password)) strength++;
  if (/[^a-zA-Z0-9]/.test(password)) strength++;
  
  return Math.min(strength, 4);
});

const passwordStrengthText = computed(() => {
  const texts = ['–û—á–µ–Ω—å —Å–ª–∞–±—ã–π', '–°–ª–∞–±—ã–π', '–°—Ä–µ–¥–Ω–∏–π', '–°–∏–ª—å–Ω—ã–π', '–û—á–µ–Ω—å —Å–∏–ª—å–Ω—ã–π'];
  return texts[passwordStrength.value] || '';
});

const getPasswordStrengthClass = (index: number) => {
  if (index <= passwordStrength.value) {
    if (passwordStrength.value <= 1) return 'bg-red-400';
    if (passwordStrength.value <= 2) return 'bg-yellow-400';
    if (passwordStrength.value <= 3) return 'bg-blue-400';
    return 'bg-green-400';
  }
  return 'bg-gray-200';
};

const isFormValid = computed(() => {
  return (
    form.value.fullName.trim() &&
    form.value.email &&
    form.value.email.includes('@') &&
    form.value.password.length >= 8 &&
    passwordsMatch.value &&
    form.value.agreeTerms
  );
});

const handleRegister = async () => {
  if (!isFormValid.value) return;

  try {
    await authStore.register({
      fullName: form.value.fullName,
      email: form.value.email,
      password: form.value.password,
    });

    // Redirect to dashboard after successful registration
    router.push('/profile');
  } catch (error) {
    // Error is handled by the store
    console.error('Registration failed:', error);
  }
};

// Clear any existing errors when component mounts
authStore.clearError();
</script>
